import React from 'react';
import Table from '../shared/table/Table';
import FormDialog from '../shared/FormDialog';
import Form from './Form';
import compose from '../../compose';
import withStore from '../../consumers/withStore';
import withUser from '../../consumers/withUser';

const ROWS = [
  { field: 'name', disablePadding: true, label: 'Name' },
  { field: '_assignedTo.name', label: 'Assigned to', type: 'enum', data: 'users', idField: "assignedToId", sortable: false },
  { field: '_state.name', label: 'State', type: 'enum', data: 'states', idField: "stateId", sortable: false },
];

const HEADERS = [
  { label: 'Name', key: 'name' },
  { label: 'Assigned to', key: 'assignedTo.name' },
  { label: 'State', key: 'state.name' },
];

const FILTERS = [];

class TableWrapper extends React.Component {
  state = {
    x: null,
    isOpen: false
  }

  handleClose = () => {
    this.setState({ isOpen: false });
  }

  handleOpen = () => {
    this.setState({ isOpen: true });
  }

  handleChange = async (filters, page, sort) => {
    try {
      return await this.props.store.getTasks(this.props.projectId, filters, page, sort);
    }
    catch (error) {
      this.props.user.showError(error);
    }
  }

  onEdit = (x) => {
    this.setState({ x, isOpen: true })
  }

  isDisabled = x => {
    return this.props.user.user._id !== x.userId && this.props.user.user._id !== x.assignedToId;
  }

  render() {
    const { deleteTask, project } = this.props.store;
    const filename = project ? `${project.name}_tasks` : null;

    return (
      <div>
        <Table
          rows={ROWS}
          data='tasks'
          onChange={this.handleChange}  
          onDelete={deleteTask}
          title='Tasks'
          onEdit={this.onEdit}
          store={this.props.store}
          filters={FILTERS}
          isDisabled={this.isDisabled}
          filename={filename}
          headers={HEADERS}
        />
        <FormDialog
          title="Edit task"
          onClose={this.handleClose}
          isOpen={this.state.isOpen}
        >
          <Form
            data={this.state.x}
            onSubmit={this.handleClose}
          />
        </FormDialog>
      </div>
    );
  }
}

export default compose(
  withStore,
  withUser,
)(TableWrapper);