import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import { getStyles } from "./style";
import AttachFileIcon from '@material-ui/icons/AttachFile';
import IconButton from './IconButton';

const styles = theme => ({
  ...getStyles(theme),
  button: {
    marginTop: theme.spacing.unit,
    marginBottom: theme.spacing.unit
  }
});

class ImageAvatars extends React.Component {
  constructor(props) {
    super(props);

    this.inputRef = React.createRef();
  }

  onChange = (event) => {
    event.preventDefault();

    const data = event.target.files[0];
    this.props.onChange(this.props.field, data)

    // const reader = new FileReader();
    // reader.onloadend = async () => {
    //   const data = reader.result;
    //   console.log(data)
    //   this.props.onChange(this.props.field, data);
    // }
    // reader.readAsArrayBuffer(event.target.files[0]);
  }

  onClick = () => {
    if(!this.props.disabled) {
      this.inputRef.current.click();
    }
  }

  render() {
    const { classes, value, disabled, mimeType } = this.props;

    return (
      <div>
        <IconButton className={classes.button} tooltip="Attach file" color={value ? 'primary' : 'default'} onClick={this.onClick} disabled={disabled}>
          <AttachFileIcon />
        </IconButton>
        <input
          ref={this.inputRef}
          style={{ display: "none" }}
          type="file"
          accept={mimeType}
          onChange={this.onChange}
        />
      </div>
    );
  }
}

ImageAvatars.propTypes = {
  classes: PropTypes.object.isRequired,
  field: PropTypes.string.isRequired,
  onChange: PropTypes.func.isRequired,
  value: PropTypes.string.isRequired,
  disabled: PropTypes.bool,
  mimeType: PropTypes.string
};

export default withStyles(styles)(ImageAvatars);
