import React, { Component } from "react";
import PropTypes from "prop-types";
import Paper from '@material-ui/core/Paper';
import { withStyles } from "@material-ui/core/styles";
import QrReader from "react-qr-reader";
import Button from "@material-ui/core/Button";
import withStore from "../../consumers/withStore";
import withUser from "../../consumers/withUser";
import Typography from "@material-ui/core/Typography";
import compose from "../../compose";
import { getStyles } from "../shared/style";

const styles = theme => ({
  ...getStyles(theme)
});

class LegacyModeExample extends Component {
  constructor(props) {
    super(props);
    this.state = {
      delay: 500,
      legacy: true
    };

    this.handleScan = this.handleScan.bind(this);
    this.openImageDialog = this.openImageDialog.bind(this);

    this.myRef = React.createRef();
  }
  handleScan(result) {
    if (result) {
      const r = result.split('.');
      if(r.length === 2) {
        try {
          this.props.user.verify(r[0], r[1]);
        } catch (error) {
          this.props.user.showError(error);
        }
      }
    }
  }
  handleError(err) {
    console.error(err);
  }
  openImageDialog() {
    this.setState({ legacy: true }, () => {
      this.myRef.current.openImageDialog();
    });
  }

  render() {
    const { classes } = this.props;

    const previewStyle = {
      height: 320,
      width: 320,
    };

    return (
      <div className={classes.row}>
        <div className={classes.col}>
          <div className={classes.center}>
            <QrReader
              ref={this.myRef}
              delay={this.state.delay}
              style={previewStyle}
              onError={this.handleError}
              onScan={this.handleScan}
              legacyMode={this.state.legacy}
            />
            <Button
              variant="contained"
              color="primary"
              className={classes.submit}
              onClick={this.openImageDialog}
            >
              Scan
            </Button>
          </div>
        </div>
        <div className={classes.col}>
          <Paper className={classes.paper}>
            <Typography variant='h6'>
              Data
            </Typography>

            {this.props.user.signature && 
              <div>
                <Typography variant='body1'>
                  {this.props.user.signature.data}
                </Typography>
              </div>
            }
          </Paper>
        </div>
      </div>
    );
  }
}

LegacyModeExample.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default compose(
  withStyles(styles),
  withStore,
  withUser
)(LegacyModeExample);
