import React from "react";
import config from "../../config";
import GithubIcon from "../shared/icons/GithubIcon";
import Button from '@material-ui/core/Button';

const GithubButton = props => {
  const handleClick = () => {
    const url = `https://github.com/login/oauth/authorize?client_id=${config.github.id}&scope=user&redirect_uri=${process.env.REACT_APP_HOMEPAGE}?provider=1`;
    window.location.replace(url);
  };

  if(!config.github) {
    return null;
  }

  return (
    <Button fullWidth={props.fullWidth} variant="outlined" color="primary" tooltip="Github" onClick={handleClick} disabled={props.disabled}>
      <GithubIcon fontSize="large" /> <span style={{ width: 200}}>{props.title}</span>
    </Button>
  );
};

export default GithubButton;
