import React from 'react';
import PropTypes from "prop-types";
import Table from '../shared/table/Table';
import FormDialog from '../shared/FormDialog';
import Form from './Form';

import compose from '../../compose';
import withStore from '../../consumers/withStore';
import withUser from '../../consumers/withUser';
import moment from 'moment';

const ROWS = [
  { field: 'name', disablePadding: true, label: 'Name' },
  { field: '_eventType.name', label: 'Type', type: 'enum', data: 'eventTypes', idField: "eventTypeId" },
  { field: 'start', type: 'date', label: 'Start', filterable: false },
  { field: 'end', type: 'date', label: 'End', filterable: false },
  { field: '_user.name', label: 'User', type: 'enum', data: 'users', idField: "userId" }
];

const HEADERS = [
  { label: 'Name', key: 'name' },
  { label: 'Type', key: 'eventType.name' },
  { label: 'Start', key: 'start' },
  { label: 'End', key: 'end' },
  { label: 'User', key: 'user.name'},
];

const FILTERS = [
  {
    label: 'Period', field: 'period', data: [
      { _id: 1, name: "today", filters: [{ field: 'start', op: 'lte', value: moment().endOf('day').toDate() }, { field: 'start', op: 'gte', value: moment().startOf('day').toDate() }] },
      { _id: 2, name: "this week", filters: [{ field: 'start', op: 'lte', value: moment().endOf('week').toDate() }, { field: 'start', op: 'gte', value: moment().startOf('week').toDate() }] },
      { _id: 3, name: "last week", filters: [{ field: 'start', op: 'lte', value: moment().subtract(1, 'weeks').endOf('week').toDate() }, { field: 'start', op: 'gte', value: moment().subtract(1, 'weeks').startOf('week').toDate() }] },
      { _id: 4, name: "this month", filters: [{ field: 'start', op: 'lte', value: moment().endOf('month').toDate() }, { field: 'start', op: 'gte', value: moment().startOf('month').toDate() }] },
      { _id: 5, name: "last month", filters: [{ field: 'start', op: 'lte', value: moment().subtract(1, 'month').endOf('month').toDate() }, { field: 'start', op: 'gte', value: moment().subtract(1, 'month').startOf('month').toDate() }] },
    ]
  }
];

class TableWrapper extends React.Component {
  constructor(props) {
    super(props);
    
    this.state = {
      x: null,
      isOpen: false
    }
  }

  handleChangeClient = (name, value) => {
    this.setState({ clientId: value });
  }

  handleClose = () => {
    this.setState({ isOpen: false });
  }

  handleOpen = () => {
    this.setState({ isOpen: true });
  }

  handleChange = async (filters, page) => {
    try {
      return await this.props.store.getEvents(filters, page);
    }
    catch (error) {
      this.props.user.showError(error);
    }
  }

  onEdit = async (x) => {
    this.setState({ x, isOpen: true })
  }

  isDisabled = x => {
    return this.props.user.user._id !== x.userId;
  }

  render() {
    const { deleteEvent } = this.props.store;

    return (
      <div>
        <Table
          rows={ROWS}
          data='events'
          onChange={this.handleChange}
          sortable={false}
          onDelete={deleteEvent}
          title='Events'
          onEdit={this.onEdit}
          store={this.props.store}
          filters={FILTERS}
          isDisabled={this.isDisabled}
          headers={HEADERS}
        />
        <FormDialog
          title="Edit event"
          onClose={this.handleClose}
          isOpen={this.state.isOpen}
        >
          <Form
            data={this.state.x}
            onSubmit={this.handleClose}
          />
        </FormDialog>
      </div>
    );
  }
}

TableWrapper.propTypes = {
  filters: PropTypes.array.isRequired
};

export default compose(
  withStore,
  withUser,
)(TableWrapper);