import React from "react";
import PropTypes from "prop-types";
import FullCalendar from "@fullcalendar/react";
import dayGridPlugin from "@fullcalendar/daygrid";
import timeGridPlugin from "@fullcalendar/timegrid";
import interactionPlugin from "@fullcalendar/interaction"; // needed for dayClick
import { withTheme } from '@material-ui/core/styles';
// import withStyles from '@material-ui/core/styles/withStyles';

// must manually import the stylesheets for each plugin
import "@fullcalendar/core/main.css";
import "@fullcalendar/daygrid/main.css";
import "@fullcalendar/timegrid/main.css";
import "./Calendar.css";

class Calendar extends React.Component {
  calendarComponentRef = React.createRef();

  state = {
    defaultView: "dayGridMonth",
    defaultDate: new Date()
  };

  eventDataTransform = event => {
    event.id = event._id;
    event.title = event[this.props.textField];
    event.color = this.props.getColor(event);
    return event;
  };

  render() {
    const { defaultDate, defaultView } = this.state;

    return (
      <div className="Calendar">
        <FullCalendar
          defaultView={defaultView}
          defaultDate={defaultDate}
          header={{
            left: "today, prev, next, title",
            // right: "dayGridMonth, timeGridWeek, timeGridDay,listWeek"
            right: "dayGridMonth, timeGridWeek"
          }}
          plugins={[dayGridPlugin, timeGridPlugin, interactionPlugin]}
          ref={this.calendarComponentRef}
          weekends
          events={this.props.data.data}
          dateClick={this.handleDateClick}
          eventClick={this.handleEventClick}
          eventDataTransform={this.eventDataTransform}
          datesRender={this.handleViewDisplay}
          eventColor={this.props.theme.palette.primary.main}
        />
      </div>
    );
  }

  handleDateClick = (args) => {
    this.props.onSelect(null, args.date);
  };

  handleEventClick = args => {
    const event = this.props.data.data.find(x => x._id === args.event.id);
    this.props.onSelect(event, args.date);
  };

  handleViewDisplay = args => {
    const filters = [
      { field: "start", op: "gte", value: args.view.activeStart },
      { field: "end", op: "lte", value: args.view.activeEnd }
    ];
    this.props.onChange(filters, { limit: 200, skip: 0 }, null);
    this.setState({
      defaultView: args.view.type,
      defaultDate: args.view.activeStart
    });
  };
}

Calendar.propTypes = {
  onSelect: PropTypes.func.isRequired,
  textField: PropTypes.string.isRequired,
  onChange: PropTypes.func.isRequired,
  data: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired,
  getColor: PropTypes.func.isRequired
};

export default withTheme()(Calendar);
