import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import FavoriteIcon from "@material-ui/icons/Favorite";
import MessageIcon from "@material-ui/icons/Message";
import IconButton from "../IconButton";
import Paper from "@material-ui/core/Paper";
import Button from "@material-ui/core/Button";
import ActionsMenu from "../ActionsMenu";
import Tooltip from '@material-ui/core/Tooltip';
import Card from "./Card";
import ListToolbar from "./ListToolbar";
import _ from "lodash";
import Progress from "../Progress";
import FilterDialog, { buildFilters } from "../FilterDialog";
import { withTheme } from '@material-ui/core/styles';

const styles = theme => ({
  listWrapper: {
    position: "relative"
  },
  list: {
    // paddingTop: theme.spacing.unit,
    // paddingBottom: theme.spacing.unit,
    display: "flex",
    flexWrap: "wrap"
  }
});

class ListComponent extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      isOpenFilter: false,
      filters: buildFilters(props.rows, props.filters, props.defaultFilters)
    };

    this.exportRef = React.createRef();
  }

  componentDidMount() {
    this.props.onChange(this.state.filters);
  }

  handleOpenFilter = () => {
    this.setState({ isOpenFilter: true });
  };

  handleCloseFilter = () => {
    this.setState({ isOpenFilter: false });
  };

  onDelete = async id => {
    const { onDelete, onChange } = this.props;

    await onDelete(id);
    onChange(this.state.filters);
  };

  handleConfirmFilter = state => {
    const filters = buildFilters(this.props.rows, this.props.filters, state);
    this.props.onChange(filters);
    this.setState({ filters, isOpenFilter: false });
  };

  handleRefresh = () => {
    this.props.onChange(this.state.filters);
  };

  render() {
    const {
      data,
      primaryTextField,
      secondaryTextField,
      onEdit,
      isDisabled,
      classes,
      onLike,
      isLiked,
      avatarField,
      defaultAvatarField,
      tooltipField,
      onMsg,
      cntField,
      theme,
      onViz
    } = this.props;

    return (
      <Paper className={classes.listWrapper}>
        <ListToolbar
          onFilter={this.handleOpenFilter}
          isFilterSet={this.state.filters.length > 0}
          actions={this.props.actions}
          title={this.props.title}
          onRefresh={this.handleRefresh}
        />
        <Progress isLoading={data.isLoading} />
        <div className={classes.list}>
          {data.data.map(x => {
            return (
              <Card
                content={this.props.getContent(x)}
                primary={_.get(x, primaryTextField)}
                secondary={_.get(x, secondaryTextField)}
                avatar={_.get(x, avatarField)}
                defaultAvatar={_.get(x, defaultAvatarField)}
                tooltip={_.get(x, tooltipField)}
                onLike={() => onLike(x)}
                onViz={() => onViz(x)}
                actions={
                  <ActionsMenu
                    disabled={isDisabled(x)}
                    onEdit={() => onEdit(x)}
                    onDelete={() => this.onDelete(x)}
                  />
                }
                cardActions={
                  <>
                    <Tooltip title="Like">
                      <Button
                        onClick={() => onLike(x._id)}
                        style={{ color: isLiked(x)? theme.palette.primary.main : 'rgba(0, 0, 0, 0.54)' }}
                      >
                        <FavoriteIcon />
                        {_.get(x, cntField)}
                      </Button>
                    </Tooltip>
                    <IconButton tooltip="Send message" onClick={() => onMsg(x)}>
                      <MessageIcon />
                    </IconButton>
                  </>
                }
              />
            );
          })}
        </div>
        <FilterDialog
          isOpen={this.state.isOpenFilter}
          onClose={this.handleCloseFilter}
          onConfirm={this.handleConfirmFilter}
          store={this.props.store}
          rows={this.props.rows}
          filters={this.props.filters}
          defaultState={this.props.defaultFilters}
        />
      </Paper>
    );
  }
}

ListComponent.propTypes = {
  classes: PropTypes.object.isRequired,
  primaryTextField: PropTypes.string.isRequired,
  secondaryTextField: PropTypes.string.isRequired,
  cntField: PropTypes.string.isRequired,
  data: PropTypes.array.isRequired,
  // onClick: PropTypes.func.isRequired,
  onChange: PropTypes.func.isRequired,
  onEdit: PropTypes.func.isRequired,
  onDelete: PropTypes.func.isRequired,
  isDisabled: PropTypes.func.isRequired,
  onLike: PropTypes.func.isRequired,
  onMsg: PropTypes.func.isRequired,
  isLiked: PropTypes.func.isRequired,
  avatarTextField: PropTypes.string.isRequired,
  defaultAvatarField: PropTypes.string.isRequired,
  tooltipField: PropTypes.string.isRequired,
  actions: PropTypes.element,
  store: PropTypes.object.isRequired,
  rows: PropTypes.array.isRequired,
  filters: PropTypes.array.isRequired,
  title: PropTypes.string,
  getContent: PropTypes.func.isRequired,
  onGet: PropTypes.func.isRequired
};

export default withTheme()(withStyles(styles)(ListComponent));
