import React from 'react';
import CardList from '../shared/list/CardList';
import compose from '../../compose';
import withStore from '../../consumers/withStore';
import withUser from '../../consumers/withUser';
import FormDialog from '../shared/FormDialog';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import { withRouter } from "react-router";
import Form from './Form';


const styles = theme => ({
  list: {
    marginTop: theme.spacing.unit
  },
});

const _rows = [
  { field: 'user.name', label: 'Tag', type: 'enum', data: 'tagTypes', idField: "typesId" },
];

class ListWrapper extends React.Component {
  state = {
    x: null,
    isOpen: false
  }

  handleClose = () => {
    this.setState({ isOpen: false });
  }

  handleOpen = () => {
    this.setState({ isOpen: true });
  }

  handleChange = async (filters) => {
    try {
      await this.props.store.getTags(filters);
    }
    catch (error) {
      this.props.user.showError(error);
    }
  }

  handleEdit = (x) => {
    this.setState({ x, isOpen: true })
  }

  handleDelete = async (x) => {
    const { deleteTag } = this.props.store;
    try {
      await deleteTag(x);
    }
    catch (error) {
      this.props.user.showError(error);
    }
  }

  isDisabled = x => {
    return this.props.user.user._id !== x.userId;
  }

  handleLike = async (id) => {
    const { likeTag } = this.props.store;
    const { user } = this.props.user;

    try {
      await likeTag(id, user);
    }
    catch (error) {
      this.props.user.showError(error);
    }
  }

  handleMsg = (x) => {
    const { user } = this.props.user;
    window.open(`mailto:${x._user.email}?subject=APP: ${x.name}&body=%0D%0A${user.name}`, "_self");
  }

  isLiked = x => {
    return x.likes.includes(this.props.user.user._id);
  }

  getContent = (x) => {
    return (
      <>
        <Typography variant="subtitle1" color="textPrimary">
          {x.name}
        </Typography>
        <Typography variant="body2" color="textSecondary" component="p">
          {x._tagTypesStr || 'žiadne'}
        </Typography>
      </>
    );
  }

  getFilters = () => [
    {
      label: 'Likes', field: 'likes', data: [
        { _id: 1, name: "Me", filters: [{ field: 'likes', value: this.props.user.user._id }] },
      ]
    }
  ];

  handleViz = async x => {
    return x.imgId ? await this.props.store.getFile(x.imgId) : null;
  }

  render() {
    const { classes } = this.props;
    const { tags } = this.props.store;

    return (
      <div className={classes.list}>
        <CardList
          data={tags}
          getContent={this.getContent}
          primaryTextField="_user.name"
          secondaryTextField="_dateStr"
          avatarField="_user.avatar"
          tooltipField="_user.name"
          cntField="likesCnt"
          onChange={this.handleChange}
          onEdit={this.handleEdit}
          onDelete={this.handleDelete}
          isDisabled={this.isDisabled}
          onLike={this.handleLike}
          isLiked={this.isLiked}
          filters={this.getFilters()}
          onViz={this.handleViz}
          rows={_rows}
          store={this.props.store}
          title="Tags"
          onMsg={this.handleMsg}
        />
        <FormDialog
          title="Edit tag"
          onClose={this.handleClose}
          isOpen={this.state.isOpen}
        >
          <Form
            data={this.state.x}
            onSubmit={this.handleClose}
          />
        </FormDialog>
      </div>
    );
  }
}

export default compose(
  withStyles(styles),
  withStore,
  withUser,
  withRouter
)(ListWrapper);