import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import DatePicker from "../shared/DatePicker";
import TextInput from "../shared/TextInput";
import Select from "../shared/Select";
import Form from "../shared/form/Form";

import withStore from "../../consumers/withStore";
import withUser from "../../consumers/withUser";
import compose from "../../compose";
import moment from 'moment';

const styles = theme => ({
  nameTextInput: {
    display: "flex",
    flex: 1
  }
});

class FormWrapper extends React.Component {
  constructor(props) {
    super(props);

    this.state = props.data || this.getInitState(props);
  }

  getInitState = (props) => {
    const date = props.date || moment().add(1,'days').toDate();

    return {
      start: date,
      end: date,
      name: "",
      eventTypeId: 1
    }
  }

  handleChange = (name, value) => {
    this.setState({ [name]: value });
  };

  handleChangeStart = (name, value) => {
    if(value.getTime() > this.state.end.getTime()) {
      this.setState({ start: value, end: value})
    }
    else {
      this.setState({ start: value });
    }
  }

  handleChangeEnd = (name, value) => {
    if(value.getTime() < this.state.start.getTime()) {
      this.setState({ start: value, end: value})
    }
    else {
      this.setState({ end: value });
    }
  }

  handleSubmit = async () => {
    const {
      addEvent,
      refreshEvents,
      editEvent
    } = this.props.store;
    const { user } = this.props.user;

    try {
      if (this.state._id) {
        await editEvent(this.state, user);
      } else {
        await addEvent(this.state, user);
      }
      refreshEvents();
      this.setState(this.getInitState(this.props));
      if (this.props.onSubmit) this.props.onSubmit();
    } catch (error) {
      this.props.user.showError(error);
    }
  };

  render() {
    const { classes, disabled } = this.props;
    const { name, eventTypeId, start, end } = this.state;
    const { eventTypes } = this.props.store;

    return (
      <Form 
        onSubmit={this.handleSubmit} 
        inline
        disabled={disabled}
      >
        <div className={classes.nameTextInput}>
          <TextInput
            label="Name"
            value={name}
            field="name"
            onChange={this.handleChange}
            fullWidth
            required
            disabled={disabled}
          />
        </div>
        <Select
          label="Type"
          value={eventTypeId}
          field="eventTypeId"
          onChange={this.handleChange}
          data={eventTypes.data}
          textField="name"
          required
          disabled={disabled}
        />
        <div className={classes.nameTextInput}>
          <DatePicker
            label="Start"
            value={start}
            field="start"
            onChange={this.handleChangeStart}
            required
            disabled={disabled}
          />
          <DatePicker
            label="End"
            value={end}
            field="end"
            onChange={this.handleChangeEnd}
            required
            disabled={disabled}
          />
        </div>
      </Form>
    );
  }
}

FormWrapper.propTypes = {
  classes: PropTypes.object.isRequired,
  data: PropTypes.object,
  onSubmit: PropTypes.func,
  date: PropTypes.instanceOf(Date),
  disabled: PropTypes.bool
};

export default compose(withStyles(styles), withStore, withUser)(FormWrapper);
