import React from "react";
import StoreContext from "../contexts/StoreContext";
import { storage } from "kd-web";
import * as storeUtils from "../utils/storeUtils";
import * as authUtils from "../utils/authUtils";

let _isInit;
class StoreProvider extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      timeRecords: {
        total: 0,
        data: [],
        filters: [],
        page: null,
        isLoading: false
      },
      events: {
        total: 0,
        data: [],
        filters: [],
        page: null,
        isLoading: false
      },
      projects: {
        total: 0,
        data: [],
        filters: [],
        isLoading: false
      },
      project: null,
      client: null,
      stats: [],
      users: {
        total: 0,
        data: []
      },
      tasks: {
        total: 0,
        data: [],
        isLoading: false
      },
      clients: {
        total: 0,
        data: [],
        isLoading: false
      },
      states: {
        total: 3,
        data: [
          { _id: 1, name: "To do" },
          { _id: 2, name: "Doing" },
          { _id: 3, name: "Done" }
        ]
      },
      eventTypes: {
        total: 1,
        data: [
          { _id: 1, name: "HO" }
        ]
      },
      projectId: null,
      tags: {
        total: 0,
        data: [],
        filters: [],
        isLoading: false
      },
      tagTypes: {
        total: 1,
        data: [
          { _id: 1, name: "kompot" },
          { _id: 2, name: "polievka" },
          { _id: 3, name: "hlavne jedlo" }
        ]
      },
      activities: {
        total: 0,
        data: [],
        isLoading: false
      },
    };
  }

  getProjects = async () => {
    try {
      this.setState(state => ({ projects: { ...state.projects, isLoading: true } }));
      const projects = await storeUtils.getProjects();
      projects.isLoading = false;
      this.setState({ projects });
    } catch (error) {
      throw error;
    }
  };

  getTags = async (filters) => {
    try {
      this.setState(state => ({ tags: { ...state.tags, isLoading: true } }));
      const tags = await storeUtils.getTags(filters, this.state.tagTypes);
      tags.isLoading = false;
      this.setState({ tags });
    } catch (error) {
      this.setState(state => ({ tags: { ...state.tags, isLoading: false } }));
      throw error;
    }
  }

  getActivities = async (group, filters, page, sort) => {
    try {
      this.setState(state => ({ activities: { ...state.activities, isLoading: true } }));
      const activities = await storeUtils.getActivities(group, filters, page, sort);
      activities.isLoading = false;
      this.setState({ activities });
    } catch (error) {
      this.setState(state => ({ activities: { ...state.activities, isLoading: false } }));
      throw error;
    }
  }

  handleChange = (name, value) => {
    this.setState({ [name]: value });
  };

  getProject = async id => {
    try {
      const project = await storeUtils.getProject(id);

      const users = [];
      for (let x of project.collaborators) {
        const user = await authUtils.getUserCached(x);
        users.push(user);
      }
      this.setState({ project, users: { total: users.length, data: users } });
    } catch (error) {
      throw error;
    }
  };

  getClient = async id => {
    try {
      const client = await storeUtils.getClient(id);
      this.setState({ client });
    } catch (error) {
      throw error;
    }
  };

  getClients = async () => {
    try {
      this.setState(state => ({ clients: { ...state.clients, isLoading: true } }));
      const clients = await storeUtils.getClients();
      clients.isLoading = false;
      this.setState({ clients });
    } catch (error) {
      throw error;
    }
  };

  refreshTimeRecords = async () => {
    try {
      this.setState(state => ({ timeRecords: { ...state.timeRecords, isLoading: true } }));
      const { filters, page } = this.state.timeRecords;
      const projects = await storeUtils.getProjects();
      const timeRecords = await storeUtils.getTimeRecords(
        projects.data,
        filters,
        page
      );
      const stats = await storeUtils.getStats(filters);
      timeRecords.isLoading = false;
      this.setState({ timeRecords, stats, projects });
    } catch (error) {
      throw error;
    }
  };

  refreshEvents = async () => {
    try {
      this.setState(state => ({ events: { ...state.events, isLoading: true } }));
      const { filters, page } = this.state.events;
      const events = await storeUtils.getEvents(
        this.state.eventTypes.data,
        filters,
        page
      );
      events.isLoading = false;
      this.setState({ events });
    } catch (error) {
      throw error;
    }
  };

  getEvents = async (filters, page) => {
    try {
      this.setState(state => ({ events: { ...state.events, isLoading: true } }));
      const users = await authUtils.getUsers([]);
      const events = await storeUtils.getEvents(
        this.state.eventTypes.data, 
        filters,
        page
      );
      events.isLoading = false;
      this.setState({ events, users });
      return events;
    } catch (error) {
      throw error;
    }
  };  

  refreshTasks = async () => {
    try {
      this.setState(state => ({ tasks: { ...state.tasks, isLoading: true } }));
      const { filters, page, sort } = this.state.tasks;
      const states = this.state.states.data;
      const tasks = await storeUtils.getTasks(states, filters, page, sort);
      tasks.isLoading = false;
      this.setState({ tasks });
    } catch (error) {
      throw error;
    }
  };

  getTasks = async (projectId, filters, page, sort) => {
    try {
      this.setState(state => ({ tasks: { ...state.tasks, isLoading: true } }));
      const f = [
        ...filters,
        { field: "projectId", op: "eq", value: projectId }
      ];
      const states = this.state.states.data;
      const tasks = await storeUtils.getTasks(states, f, page, sort);
      tasks.isLoading = false;
      this.setState({ tasks });
      return tasks;
    } catch (error) {
      throw error;
    }
  };

  getOpenedTasks = async (projectId, user) => {
    try {
      const filters = [
        { field: "stateId", op: "ne", value: 3 },
        { field: "assignedToId", value: user._id }
      ];
      await this.getTasks(projectId, filters, undefined);
    } catch (error) {
      throw error;
    }
  };

  likeTag = (id, user) => {
    this.setState(state => ({ tags: { ...state.tags, data: state.tags.data.map(x => {
      if(x._id === id) {
        const index = x.likes.indexOf(user._id);
        if (index > -1) {
          x.likes.splice(index, 1);
          x.likesCnt = x.likesCnt-1;
        }
        else {
          x.likes.push(user._id);
          x.likesCnt = x.likesCnt+1;
        }
        storeUtils.editTag(x);
      }

      return x;
    }) } }));
  }

  getTimeRecords = async (filters, page) => {
    try {
      this.setState(state => ({ timeRecords: { ...state.timeRecords, isLoading: true } }));
      const projects = await storeUtils.getProjects();
      const users = await authUtils.getUsers([]);
      const f = [
        ...filters,
        { field: "projectId", op: "in", value: projects.data.map(x => x._id) }
      ];
      const timeRecords = await storeUtils.getTimeRecords(
        projects.data,
        f,
        page
      );
      const stats = await storeUtils.getStats(f);
      timeRecords.isLoading = false;
      this.setState({ timeRecords, stats, projects, users });
      return timeRecords;
    } catch (error) {
      throw error;
    }
  };

  handleChangeCollection = collection => {
    console.log("CHANGED: " + collection);
  };

  render() {
    const { children } = this.props;
    if (!_isInit && storage.getUser()) {
      _isInit = true;
      storeUtils.initHub(this.handleChangeCollection);
    }

    return (
      <StoreContext.Provider
        value={{
          addTimeRecord: storeUtils.addTimeRecord,
          addEvent: storeUtils.addEvent, 
          getTimeRecords: this.getTimeRecords,
          getEvents: this.getEvents,
          refreshEvents: this.refreshEvents, 
          refreshTimeRecords: this.refreshTimeRecords,
          deleteTimeRecord: storeUtils.deleteTimeRecord,
          deleteEvent: storeUtils.deleteEvent,
          addProject: storeUtils.addProject,
          addClient: storeUtils.addClient,
          addTag: storeUtils.addTag,
          getClients: this.getClients,
          editClient: storeUtils.editClient,
          editTag: storeUtils.editTag,
          deleteClient: storeUtils.deleteClient,
          getProjects: this.getProjects,
          deleteProject: storeUtils.deleteProject,
          deleteTag: storeUtils.deleteTag,
          addTask: storeUtils.addTask,
          editTask: storeUtils.editTask,
          deleteTask: storeUtils.deleteTask,
          refreshTasks: this.refreshTasks,
          editProject: storeUtils.editProject,
          editTimeRecord: storeUtils.editTimeRecord,
          editEvent: storeUtils.editEvent,
          getTasks: this.getTasks,
          getProject: this.getProject,
          getClient: this.getClient,
          getOpenedTasks: this.getOpenedTasks,
          handleChange: this.handleChange,
          getTags: this.getTags,
          likeTag: this.likeTag,
          getActivities: this.getActivities,
          getFile: storeUtils.getFile,
          ...this.state
        }}
      >
        {children}
      </StoreContext.Provider>
    );
  }
}

export default StoreProvider;
