import React from 'react';
import PropTypes from "prop-types";
import Table from '../shared/table/Table';
import FormDialog from '../shared/FormDialog';
import Form from './Form';

import compose from '../../compose';
import withStore from '../../consumers/withStore';
import withUser from '../../consumers/withUser';
import moment from 'moment';

const buildRows = (filters) => {
  const userFilterable = filters.find(x => x.field === 'userId' ) === undefined;

  return [
    { field: 'name', disablePadding: true, label: 'Name' },
    { field: 'date', type: 'date', label: 'Date' },
    { field: '_project.name', label: 'Project', type: 'enum', data: 'projects', idField: "projectId" },
    { field: '_task.name', label: 'Task', type: 'enum', data: 'tasks', idField: "taskId", filterable: false },
    { field: '_user.name', label: 'User', type: 'enum', data: 'users', idField: "userId", filterable:  userFilterable },
    { field: 'duration', type: 'num', label: 'Duration', filterable: false },
  ];
}

const HEADERS = [
  { label: 'Name', key: 'name' },
  { label: 'Date', key: 'date' },
  { label: 'Project', key: 'project.name' },
  { label: 'Task', key: 'task.name' },
  { label: 'User', key: 'user.name'},
  { label: 'Duration', key: 'duration' },
];

const FILTERS = [
  {
    label: 'Period', field: 'period', data: [
      { _id: 1, name: "today", filters: [{ field: 'date', op: 'lte', value: moment().endOf('day').toDate() }, { field: 'date', op: 'gte', value: moment().startOf('day').toDate() }] },
      { _id: 2, name: "this week", filters: [{ field: 'date', op: 'lte', value: moment().endOf('week').toDate() }, { field: 'date', op: 'gte', value: moment().startOf('week').toDate() }] },
      { _id: 3, name: "last week", filters: [{ field: 'date', op: 'lte', value: moment().subtract(1, 'weeks').endOf('week').toDate() }, { field: 'date', op: 'gte', value: moment().subtract(1, 'weeks').startOf('week').toDate() }] },
      { _id: 4, name: "this month", filters: [{ field: 'date', op: 'lte', value: moment().endOf('month').toDate() }, { field: 'date', op: 'gte', value: moment().startOf('month').toDate() }] },
      { _id: 5, name: "last month", filters: [{ field: 'date', op: 'lte', value: moment().subtract(1, 'month').endOf('month').toDate() }, { field: 'date', op: 'gte', value: moment().subtract(1, 'month').startOf('month').toDate() }] },
      { _id: 6, name: "this year", filters: [{ field: 'date', op: 'lte', value: moment().endOf('year').toDate() }, { field: 'date', op: 'gte', value: moment().startOf('year').toDate() }] },
      { _id: 7, name: "last year", filters: [{ field: 'date', op: 'lte', value: moment().subtract(1, 'year').endOf('month').toDate() }, { field: 'date', op: 'gte', value: moment().subtract(1, 'year').startOf('year').toDate() }] },
    ]
  }
];

let _rows;
class TableWrapper extends React.Component {
  constructor(props) {
    super(props);
    
    this.state = {
      x: null,
      isOpen: false,
    }

    _rows = buildRows(props.filters);
  }

  handleClose = () => {
    const { user } = this.props.user;
    const { projectId, getOpenedTasks } = this.props.store;
    getOpenedTasks(projectId, user);

    this.setState({ isOpen: false });
  }

  handleOpen = () => {
    this.setState({ isOpen: true });
  }

  handleChange = async (filters, page) => {
    try {
      const f = [...this.props.filters, ...filters];
      return await this.props.store.getTimeRecords(f, page);
    }
    catch (error) {
      this.props.user.showError(error);
    }
  }

  onEdit = async (x) => {
    const { user } = this.props.user;
    await this.props.store.getOpenedTasks(x.projectId, user);
    this.setState({ x, isOpen: true })
  }

  isDisabled = x => {
    return this.props.user.user._id !== x.userId;
  }

  async componentDidMount() {
    try {
      this.props.store.getClients();
    }
    catch (error) {
      this.props.user.showError(error);
    }
  }

  render() {
    const { deleteTimeRecord } = this.props.store;

    return (
      <div>
        <Table
          rows={_rows}
          data='timeRecords'
          onChange={this.handleChange}
          sortable={false}
          onDelete={deleteTimeRecord}
          title='Time records'
          onEdit={this.onEdit}
          store={this.props.store}
          filters={FILTERS}
          defaultFilters={this.props.defaultFilters}
          isDisabled={this.isDisabled}
          headers={HEADERS}
        />
        <FormDialog
          title="Edit time record"
          onClose={this.handleClose}
          isOpen={this.state.isOpen}
        >
          <Form
            data={this.state.x}
            onSubmit={this.handleClose}
          />
        </FormDialog>
      </div>
    );
  }
}

TableWrapper.propTypes = {
  filters: PropTypes.array.isRequired,
  defaultFilters: PropTypes.object
};

export default compose(
  withStore,
  withUser,
)(TableWrapper);