import React from "react";
import PropTypes from "prop-types";
import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import CssBaseline from "@material-ui/core/CssBaseline";
import FormControl from "@material-ui/core/FormControl";
import Input from "@material-ui/core/Input";
import InputLabel from "@material-ui/core/InputLabel";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import withStyles from "@material-ui/core/styles/withStyles";

import { Redirect } from "react-router-dom";
import withUser from "../../consumers/withUser";
import withStore from "../../consumers/withStore";
import compose from "../../compose";
import GithubButton from "./GithubButton";
import GoogleButton from "./GoogleButton";
import StravaButton from "./StravaButton";
import config from "../../config.json";

const styles = theme => ({
  main: {
    width: "auto",
    display: "block", // Fix IE 11 issue.
    marginLeft: theme.spacing.unit * 3,
    marginRight: theme.spacing.unit * 3,
    [theme.breakpoints.up(400 + theme.spacing.unit * 3 * 2)]: {
      width: 400,
      marginLeft: "auto",
      marginRight: "auto"
    }
  },
  paper: {
    marginTop: theme.spacing.unit * 8,
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    padding: theme.spacing.unit * 2,
    marginBottom: theme.spacing.unit * 2
  },
  avatar: {
    margin: theme.spacing.unit,
    backgroundColor: theme.palette.secondary.main,
    marginBottom: theme.spacing.unit * 2
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing.unit
  },
  submit: {
    marginTop: theme.spacing.unit * 3
  },
  forgot: {
    marginTop: theme.spacing.unit,
  }
});

class Login extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      username: "",
      password: "",
      token: "",
      disabled: false
    };
  }

  handleSubmit = async e => {
    e.preventDefault();

    this.setState({ disabled: true });

    const { username, password, token } = this.state;
    try {
      const user = await this.props.user.auth(username.trim().toLowerCase(), password, token);
      if (user === null) {
        this.props.user.showMsg("Unauthorized");
      }
    } catch (error) {
      this.props.user.showError(error);
    }

    this.setState({ disabled: false });
  };

  handleChange = e => {
    this.setState({ [e.target.name]: e.target.value });
  };

  handleClickReset = async () => {
    const { username } = this.state;

    if (username) {
      try {
        await this.props.user.reset(username.trim().toLowerCase());
        this.props.user.showMsg("Mail sended.");
      } catch (error) {
        this.props.user.showError(error);
      }
    }
  };

  render() {
    const { classes, user } = this.props;
    const { username, password, disabled, oAuth, token } = this.state;

    const { from } = this.props.location.state || {
      from: { pathname: config.home }
    };

    if (oAuth) return null;
    if (user.user) return <Redirect to={from} />;

    return (
      <main className={classes.main}>
        <CssBaseline />
        <Paper className={classes.paper}>
          <Avatar className={classes.avatar}>
            <LockOutlinedIcon />
          </Avatar>
          {/* <Typography component="h1" variant="h5">
            Sign in
          </Typography> */}

          <GithubButton fullWidth disabled={disabled} title="Sign in with Github" />
          <GoogleButton fullWidth disabled={disabled} title="Sign in with Google" />
          <StravaButton fullWidth  disabled={disabled} title="Sign in with Strava" />

          <Typography variant='body1'>
            or use your account
          </Typography>

          <form className={classes.form} onSubmit={this.handleSubmit}>
            <FormControl margin="normal" required fullWidth>
              <InputLabel htmlFor="username">Username</InputLabel>
              <Input
                id="username"
                name="username"
                autoComplete="username"
                autoFocus
                value={username}
                onChange={this.handleChange}
              />
            </FormControl>
            <FormControl margin="normal" required fullWidth>
              <InputLabel htmlFor="password">Password</InputLabel>
              <Input
                name="password"
                type="password"
                id="password"
                autoComplete="current-password"
                value={password}
                onChange={this.handleChange}
              />
            </FormControl>
            {config["2fa"] && (
              <FormControl margin="normal" fullWidth>
                <InputLabel htmlFor="password">Token</InputLabel>
                <Input
                  name="token"
                  id="token"
                  value={token}
                  onChange={this.handleChange}
                />
              </FormControl>
            )}
            {/* <FormControlLabel
              control={<Checkbox value="remember" color="primary" />}
              label="Remember me"
            /> */}
            <Button
              type="submit"
              fullWidth
              variant="contained"
              color="primary"
              className={classes.submit}
              disabled={disabled}
            >
              Sign in
            </Button>
          </form>

          {/* <Button className={classes.forgot} onClick={this.handleClickReset} disabled={disabled}>
            Forgot password?
          </Button> */}

        </Paper>
      </main>
    );
  }
}

Login.propTypes = {
  classes: PropTypes.object.isRequired
};

export default compose(withStyles(styles), withUser, withStore)(Login);
