import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import TextInput from '../shared/TextInput';
import Form from '../shared/form/Form';
import Select from '../shared/Select';
import Switch from '../shared/Switch';

import withStore from '../../consumers/withStore';
import withUser from '../../consumers/withUser';
import compose from '../../compose';
import FileInput from '../shared/FileInput';

const styles = theme => ({
  collaborators: {
    display: 'flex',
    flex: 1
  }
});

const INIT_STATE = {
  name: '',
  likes: [],
  likesCnt: 0,
  typesId: [],
  unavailable: false,
  _img: null
};

class FormWrapper extends React.Component {
  state = this.props.data || INIT_STATE;

  handleChange = (name, value) => {
    this.setState({ [name]: value });
  }

  handleSubmit = async () => {
    const { addTag, getTags, editTag, tags } = this.props.store;
    const { user } = this.props.user;

    try {
      if (this.state._id) {
        await editTag(this.state, user);
      }
      else {
        await addTag(this.state, user);
      }

      getTags(tags.filters);
      this.setState(INIT_STATE);
      if (this.props.onSubmit) this.props.onSubmit();
    }
    catch (error) {
      this.props.user.showError(error);
    }
  }

  render() {
    const { classes } = this.props;
    const { name, typesId, unavailable, _img } = this.state;
    const { tagTypes } = this.props.store;

    return (
      <Form onSubmit={this.handleSubmit} inline>
        <TextInput
          label="Name"
          value={name}
          field="name"
          onChange={this.handleChange}
          required
        />
        <div className={classes.collaborators}>
          <Select
            label="Types"
            value={typesId}
            field="typesId"
            onChange={this.handleChange}
            data={tagTypes.data}
            textField="name"
            fullWidth
            multiple
          />
        </div>
        <FileInput 
          mimeType='image/jpeg'
          value={_img}
          field='_img'
          onChange={this.handleChange}
          disabled={this.state._id !== undefined}
        />
        <Switch
          label=""
          value={unavailable}
          field="unavailable"
          onChange={this.handleChange}
        />
      </Form>
    );
  }
}

FormWrapper.propTypes = {
  classes: PropTypes.object.isRequired,
  onSubmit: PropTypes.func
};

export default compose(
  withStyles(styles),
  withStore,
  withUser
)(FormWrapper);