import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import Paper from '@material-ui/core/Paper';
import ActionsMenu from '../ActionsMenu';
import Progress from '../Progress';
import _ from 'lodash';

const styles = theme => ({
  listWrapper: {
    position: 'relative'
  }
});

class ListComponent extends React.Component {

  componentDidMount() {
    this.props.onChange();
  }

  onDelete = async (id) => {
    const { onDelete, onChange } = this.props;

    await onDelete(id);
    onChange();
  }

  render() {
    const { data, onClick, primaryTextField, secondaryTextField, onEdit, isDisabled, classes } = this.props;

    return (
      <Paper className={classes.listWrapper}>
        <Progress isLoading={data.isLoading} />
        <List>
          {data.data.map(x => (
            <ListItem key={x._id} button onClick={() => onClick(x._id)}>
              <ListItemText primary={_.get(x, primaryTextField)} secondary={secondaryTextField ? _.get(x, secondaryTextField) : null} />
              <ListItemSecondaryAction>
                <ActionsMenu 
                  disabled={isDisabled(x)}
                  onEdit={() => onEdit(x)}
                  onDelete={() => this.onDelete(x._id)}
                />
              </ListItemSecondaryAction>
            </ListItem>
          ))}
        </List>
      </Paper>
    );
  }
}

ListComponent.propTypes = {
  classes: PropTypes.object.isRequired,
  primaryTextField: PropTypes.string.isRequired,
  secondaryTextField: PropTypes.string,
  data: PropTypes.object.isRequired,
  onClick: PropTypes.func.isRequired,
  onChange: PropTypes.func.isRequired,
  onEdit: PropTypes.func.isRequired,
  onDelete: PropTypes.func.isRequired,
  isDisabled: PropTypes.func.isRequired
};

export default withStyles(styles)(ListComponent);