import React, { PureComponent } from "react";
import { withStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip } from "recharts";
import Typography from "@material-ui/core/Typography";
import compose from "../../compose";
import withStore from "../../consumers/withStore";
import { withTheme } from '@material-ui/core/styles';

const styles = theme => ({
  paper: {
    width: "100%",
    padding: theme.spacing.unit,
    overflowX: "auto",
    margin: "auto"
  },
  heading: {
    marginLeft: theme.spacing.unit
  }
});

class Example extends PureComponent {
  render() {
    const { stats } = this.props.store;
    const { theme } = this.props;

    return (
      <Paper className={this.props.classes.paper}>
        <BarChart width={800} height={300} data={stats.data}>
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="name" />
          <YAxis />
          <Tooltip />
          {/* <Legend /> */}
          <Bar dataKey="val" fill={theme.palette.primary.main} />
        </BarChart>
        <Typography variant="body2" color="textPrimary" component="p" className={this.props.classes.heading}>
            Total: {stats.sum}
          </Typography>
      </Paper>
    );
  }
}

export default compose(withStore, withStyles(styles), withTheme())(Example);
