import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import DatePicker from "../shared/DatePicker";
import TextInput from "../shared/TextInput";
import NumInput from "../shared/NumInput";
import Select from "../shared/Select";
import Form from "../shared/form/Form";

import withStore from "../../consumers/withStore";
import withUser from "../../consumers/withUser";
import compose from "../../compose";

const styles = theme => ({
  nameTextInput: {
    display: "flex",
    flex: 1
  }
});

const INIT_STATE = {
  date: new Date(),
  name: "",
  duration: 0,
  projectId: null,
  taskId: null
};

class FormWrapper extends React.Component {
  state = this.props.data || INIT_STATE;

  handleChangeProject = (name, value) => {
    const { user } = this.props.user;
    const { getOpenedTasks, handleChange } = this.props.store;
    
    getOpenedTasks(value, user);
    if(!this.props.data) {
      handleChange(name, value);
    }
    this.handleChange(name, value);
  }

  handleChange = (name, value) => {
    this.setState({ [name]: value });
  };

  handleSubmit = async () => {
    const {
      addTimeRecord,
      refreshTimeRecords,
      editTimeRecord
    } = this.props.store;
    const { user } = this.props.user;

    if (!this.state.projectId) {
      return;
    }

    try {
      if (this.state._id) {
        await editTimeRecord(this.state, user);
      } else {
        await addTimeRecord(this.state, user);
      }
      refreshTimeRecords();
      this.setState(INIT_STATE);
      if (this.props.onSubmit) this.props.onSubmit();
    } catch (error) {
      this.props.user.showError(error);
    }
  };

  render() {
    const { classes } = this.props;
    const { date, name, duration, projectId, taskId } = this.state;
    const { projects, tasks } = this.props.store;

    return (
      <Form onSubmit={this.handleSubmit} inline>
        <div className={classes.nameTextInput}>
          <TextInput
            label="Name"
            value={name}
            field="name"
            onChange={this.handleChange}
            fullWidth
            required
          />
        </div>
        <Select
          label="Project"
          value={projectId}
          field="projectId"
          onChange={this.handleChangeProject}
          data={projects.data}
          textField="name"
          required
        />
        <Select
          label="Task"
          value={taskId}
          field="taskId"
          onChange={this.handleChange}
          data={tasks.data}
          textField="name"
        />
        <DatePicker
          label="Date"
          value={date}
          field="date"
          onChange={this.handleChange}
          required
        />
        <NumInput
          label="Duration"
          value={duration}
          field="duration"
          onChange={this.handleChange}
          min={0}
          step={0.25}
          required
        />
      </Form>
    );
  }
}

FormWrapper.propTypes = {
  classes: PropTypes.object.isRequired,
  data: PropTypes.object,
  onSubmit: PropTypes.func
};

export default compose(withStyles(styles), withStore, withUser)(FormWrapper);
