import React from 'react';
import PropTypes from 'prop-types';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import MoreIcon from '@material-ui/icons/MoreVert';
import IconButton from '@material-ui/core/IconButton';
import AlertDialog from './AlertDialog';

class ActionsMenu extends React.Component {
  state = {
    anchorEl: null,
    isOpenDelete: false
  };

  handleOpenMenu = event => {
    this.setState({ anchorEl: event.currentTarget });
  };

  handleCloseMenu = () => {
    this.setState({ anchorEl: null });
  };

  handleCloseDelete = () => {
    this.setState({ isOpenDelete: false });
  }

  handleOpenDelete = () => {
    this.setState({ isOpenDelete: true, anchorEl: null });
  }

  onDelete = () => {
    this.props.onDelete();
    this.handleCloseDelete();
  }

  onEdit = () => {
    this.props.onEdit();
    this.handleCloseMenu();
  }

  render() {
    const { anchorEl, isOpenDelete } = this.state;

    return (
      <div>
        <IconButton
          aria-owns={anchorEl ? 'actions-menu' : undefined}
          aria-haspopup="true" edge="end"
          aria-label="actions"
          onClick={this.handleOpenMenu}
          disabled={this.props.disabled}
        >
          <MoreIcon />
        </IconButton>
        <Menu
          id="actions-menu"
          anchorEl={anchorEl}
          open={Boolean(anchorEl)}
          onClose={this.handleCloseMenu}
        >
          <MenuItem onClick={this.onEdit}>Edit</MenuItem>
          <MenuItem onClick={this.handleOpenDelete}>Delete</MenuItem>
        </Menu>
        <AlertDialog 
          isOpen={isOpenDelete} 
          onClose={this.handleCloseDelete} 
          onConfirm={this.onDelete} 
          title="Delete"
          description="Are you sure?"
        />
      </div>
    );
  }
}

ActionsMenu.propTypes = {
  onDelete: PropTypes.func.isRequired,
  onEdit: PropTypes.func.isRequired,
  disabled: PropTypes.bool.isRequired
};

export default ActionsMenu;