export const getProjects = async () => {
  try {
    const user = storage.getUser();
    const sort = { field: '_id', dir: 'desc' }
    const filters = [{ field: 'collaborators', op: "eq", value: user._id }];
    const projects = await store.get(PROJECTS_COLLECTION, filters, sort);
    projects.data.forEach(p => {
      p.collaborators = p.collaborators.filter(x => x !== user._id);
    })
    return projects;
  }
  catch (error) {
    throw error;
  }
}

export const getTags = async (filters, tagTypes) => {
  try {
    const location = await store.getLocation();
    location.max = null;
    const date = moment().subtract(1, 'day').toDate();

    const f = [ ...filters, {field: "location", op: "near", value: location}, {field: "date", op: "gt", value: date} ];
    const sort = { field: 'location', dir: 'asc' };
    const tags = await store.get(TAGS_COLLECTION, f, sort);
    tags.filters = filters;
    for(const x of tags.data) {
      x.date = x.date ? new Date(x.date) : null;
      x._dateStr = x.date.toLocaleString();
      x._user = await a.getUserCached(x.userId);

      let tagTypesStr = '';
      x.typesId.forEach((typeId, index) => {
        if(index !== 0) {
          tagTypesStr += ' | ';
        }
        const tagType = tagTypes.data.find(tagType => tagType._id === typeId);
        tagTypesStr += tagType.name;
      });
      x._tagTypesStr = tagTypesStr;
    }

    return tags;
  }
  catch (error) {
    throw error;
  }
}

const processValue = (group, val) => {
  switch(group.opField) {
    case 'dis':
      return `${Math.round(val/100)/10} km`;
    case 'total_elevation_gain':
      return `${val} m`;
    case 'moving_time':
      return convert.secondsToTime(val);
    default:
      return val.toString();
  }
}

const parseDate = (date) => {
  const year = date.substring(0, 4);
  const month = date.substring(4, 6);
  const day = date.substring(6, 8);
  
  return new Date(year, month, day);
}

const getMonth = (date) => {  
  const d = parseDate(date.toString());
  return d.toLocaleString('default', { month: 'long', year: 'numeric' });
}

const getYear = (date) => {
  return date.toString().substring(0, 4);
}

export const getActivities = async (group, filters, page, sort) => {
  try {
    const activities = await store.aggregation(ACTIVITIES_COLLECTION, group, filters, sort, page);

    for(const x of activities.data) {
      if(group.field === 'userId') { 
        x._user = await a.getUserCached(x._id);
      }
      else if(group.field === 'month') {
        x._id = getMonth(x._id);
      }
      else if(group.field === 'year') {
        x._id = getYear(x._id);
      }

      x.val = processValue(group, x.val);
    }
    
    return activities;
  }
  catch (error) {
    throw error;
  }
}

