const taskCache = {};
export const getTaskCached = async (id) => {
  try {
    if(!taskCache[id]) {
      taskCache[id] = await getTask(id);
    }
    return taskCache[id];
  }
  catch(error) {
    throw error;
  }
}

export const initHub = (onChangeCollection) => {
  const connection = new signalR.HubConnectionBuilder()
    .withUrl(`${config.url.store}/hub`, { accessTokenFactory: () => storage.getUser().refreshToken })
    .configureLogging(signalR.LogLevel.Error)
    .build();

  connection.start().catch(err => {
    console.error(err)
  });

  connection.on("change", onChangeCollection);
}