import React from 'react';
import List from '../shared/list/List';
import compose from '../../compose';
import withStore from '../../consumers/withStore';
import withUser from '../../consumers/withUser';
import FormDialog from '../shared/FormDialog';
import { withStyles } from '@material-ui/core/styles';
import { withRouter } from "react-router";
import Form from './Form';


const styles = theme => ({
  list: {
    marginTop: theme.spacing.unit
  },
});

class ListWrapper extends React.Component {
  state = {
    x: null,
    isOpen: false
  }

  handleClose = () => {
    this.setState({ isOpen: false });
  }

  handleOpen = () => {
    this.setState({ isOpen: true });
  }

  handleClick = async (id) => {
    const path = this.props.history.location.pathname + "/" + id;
    this.props.history.push(path);
  }

  handleChange = (filters) => {
    try {
      this.props.store.getClients();
    }
    catch (error) {
      this.props.user.showError(error);
    }
  }

  handleEdit = (x) => {
    this.setState({ x, isOpen: true })
  }

  handleDelete = async (id) => {
    const { deleteClient } = this.props.store;
    try {
      await deleteClient(id);
    }
    catch (error) {
      this.props.user.showError(error);
    }
  }

  isDisabled = x => {
    return this.props.user.user._id !== x.userId;
  }

  render() {
    const { classes } = this.props;
    const { clients } = this.props.store;

    return (
      <div className={classes.list}>
        <List
          data={clients}
          primaryTextField="name"
          onClick={this.handleClick}
          onChange={this.handleChange}
          onEdit={this.handleEdit}
          onDelete={this.handleDelete}
          isDisabled={this.isDisabled}
        />
        <FormDialog
          title="Edit client"
          onClose={this.handleClose}
          isOpen={this.state.isOpen}
        >
          <Form
            data={this.state.x}
            onSubmit={this.handleClose}
          />
        </FormDialog>
      </div>
    );
  }
}

export default compose(
  withStyles(styles),
  withStore,
  withUser,
  withRouter
)(ListWrapper);