import React from "react";
import UserContext from "../contexts/UserContext";
import * as authUtils from "../utils/authUtils";
import { storage } from "kd-web";

class UserProvider extends React.Component {
  state = {
    user: storage.getUser(),
    msg: null,
    users: {
      total: 0,
      data: []
    },
    secret: null,
    signature: null
  };

  showMsg = msg => {
    this.setState({ msg });
  };

  showError = error => {
    this.setState({ msg: error.message });
  };

  hideMsg = () => {
    this.setState({ msg: null });
  };

  auth = async (username, password, token) => {
    try {
      const user = await authUtils.auth(username, password, token);
      this.setState({ user });
      return user;
    } catch (error) {
      throw error;
    }
  };

  generateSecret = async () => {
    try {
      const secret = await authUtils.generateSecret();
      this.setState({ secret });
    } catch (error) {
      throw error;
    }
  };

  sign = async (data) => {
    try {
      const signature = await authUtils.sign(btoa(data));
      this.setState({ signature });
    } catch (error) {
      throw error;
    }
  }

  verify = async (data, signature) => {
    try {
      const res = await authUtils.verify(data, signature);
      this.setState({ signature: { data: res ? atob(data) : 'NOT VALID', signature } })
    } catch (error) {
      throw error;
    }
  }

  oAuth = async (provider, code) => {
    try {
      const user = await authUtils.oAuth(provider, code);
      this.setState({ user });
      return user;
    } catch (error) {
      throw error;
    }
  };

  updateOauth = async (provider, code) => {
    try {
      const user = await authUtils.updateOauth(provider, code);
      this.setState({ user });
    } catch (error) {
      throw error;
    }
  };

  updateUser = async (id, data) => {
    try {
      const user = await authUtils.updateUser(id, data);
      this.setState({ user });
      return user;
    } catch (error) {
      throw error;
    }
  };

  reset = async (username) => {
    try {
      await authUtils.reset(username);
    } catch (error) {
      throw error;
    }
  }

  getUsers = async filters => {
    try {
      const users = await authUtils.getUsers(filters);
      this.setState({ users });
    } catch (error) {
      throw error;
    }
  };

  hasPermission = (permission) => {
    return this.state.user && (this.state.user.permissions & permission) > 0;
  }

  render() {
    const { children } = this.props;

    return (
      <UserContext.Provider
        value={{
          auth: this.auth,
          hideMsg: this.hideMsg,
          showMsg: this.showMsg,
          showError: this.showError,
          generateSecret: this.generateSecret,
          oAuth: this.oAuth,
          updateOauth: this.updateOauth,
          getUsers: this.getUsers,
          getUser: authUtils.getUser,
          updateUser: this.updateUser,
          hasPermission: this.hasPermission,
          reset: this.reset,
          sign: this.sign,
          verify: this.verify,
          ...this.state
        }}
      >
        {children}
      </UserContext.Provider>
    );
  }
}

export default UserProvider;
