import React from "react";
import config from "../../config";
import GoogleIcon from "../shared/icons/GoogleIcon";
import Button from '@material-ui/core/Button';

const GithubButton = props => {
  const handleClick = () => {
    const url = `https://accounts.google.com/o/oauth2/v2/auth?scope=https://www.googleapis.com/auth/userinfo.profile&redirect_uri=${process.env.REACT_APP_HOMEPAGE}?provider=2&response_type=code&client_id=${config.google.id}`;
    window.location.replace(url);
  };

  if(!config.google) {
    return null;
  }

  return (
    <Button fullWidth={props.fullWidth} variant="outlined" color="primary" onClick={handleClick} disabled={props.disabled}>
      <GoogleIcon fontSize="large" /> <span style={{ width: 200}}>{props.title}</span>
    </Button>
  );
};

export default GithubButton;