import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import TextInput from "../shared/TextInput";
import Form from "../shared/form/Form";
import Button from "@material-ui/core/Button";
import Dialog from "../shared/Dialog";
import AvatarPicker from "../shared/avatar/AvatarPicker";

import withStore from "../../consumers/withStore";
import withUser from "../../consumers/withUser";
import compose from "../../compose";
import Switch from "../shared/Switch";
import { getStyles } from "../shared/style";
import config from "../../config.json";

const styles = theme => ({
  ...getStyles(theme),
  heading: {
    marginLeft: theme.spacing.unit
  },
  detailHeading: {
    marginLeft: theme.spacing.unit,
    marginTop: theme.spacing.unit * 3
  },
  actions: {
    float: "right",
    margin: theme.spacing.unit
  }
});

const INIT_STATE = {
  password: "",
  repeatedPassword: "",
  name: "",
  username: "",
  email: "",
  otp: false,
  avatar: null
};

class FormWrapper extends React.Component {
  state = {
    ...INIT_STATE,
    ...this.props.user.user,
    isOpenSecretDialog: false,
    avatarChanged: false
  };

  handleChange = (name, value) => {
    this.setState({ [name]: value });
  };

  handleChangeAvatar = (name, value) => {
    this.setState({ [name]: value, avatarChanged: true });
  };

  generateSecret = async () => {
    try {
      await this.props.user.generateSecret();
      this.handleOpenSecretDialog();
    } catch (error) {
      this.props.user.showError(error);
    }
  };

  handleOpenSecretDialog = () => {
    this.setState({ isOpenSecretDialog: true });
  };

  handleCloseSecretDialog = () => {
    this.setState({ isOpenSecretDialog: false });
  };

  handleSubmit = async () => {
    const {
      password,
      repeatedPassword,
      name,
      username,
      email,
      otp,
      avatar,
      avatarChanged
    } = this.state;
    if (password !== repeatedPassword) {
      this.props.user.showMsg("Passwords do not match.");
      return;
    }

    try {
      await this.props.user.updateUser(this.state._id, {
        name,
        username,
        email,
        password,
        otp,
        avatar: avatarChanged ? avatar : null
      });
      this.setState({ ...this.props.user.user });
      this.props.user.showMsg("Ok.");
    } catch (error) {
      this.props.user.showError(error);
    }
  };

  render() {
    const { classes, disabled } = this.props;
    const {
      password,
      repeatedPassword,
      username,
      name,
      email,
      otp,
      avatar
    } = this.state;

    return (
      <>
        <Form onSubmit={this.handleSubmit} disabled={disabled}>
          <AvatarPicker
            field="avatar"
            onChange={this.handleChangeAvatar}
            value={avatar}
            disabled={disabled}
            tooltip={name || ''}
          />
          <div className={classes.row}>
            <div className={classes.col}>
              <TextInput
                label="Name"
                value={name}
                field="name"
                onChange={this.handleChange}
                required
                disabled={disabled}
              />
            </div>
            <div className={classes.col}>
              <TextInput
                label="Email"
                value={email}
                field="email"
                onChange={this.handleChange}
                disabled={disabled}
              />
            </div>
          </div>
          <div className={classes.row}>
            <div className={classes.col}>
              <TextInput
                label="Username"
                value={username}
                field="username"
                onChange={this.handleChange}
                required
                disabled={true}
              />
            </div>
            <div className={classes.col}>
              <TextInput
                label="Password"
                value={password}
                field="password"
                type="new-password"
                onChange={this.handleChange}
                disabled={disabled}
              />
            </div>
            <div className={classes.col}>
              <TextInput
                label="Password again"
                value={repeatedPassword}
                field="repeatedPassword"
                type="password"
                onChange={this.handleChange}
                disabled={disabled}
              />
            </div>
          </div>
          {config["2fa"] && (
            <div className={classes.row}>
              <div className={classes.col}>
                <div>
                  <Switch
                    label="2FA"
                    value={otp}
                    field="otp"
                    onChange={this.handleChange}
                    disabled={disabled}
                  />
                  <Button
                    color="primary"
                    disabled={disabled || !this.state.otp}
                    onClick={this.generateSecret}
                  >
                    Generate secret
                  </Button>
                </div>
              </div>
            </div>
          )}
        </Form>
        <Dialog
          title="Secret"
          isOpen={this.state.isOpenSecretDialog}
          onConfirm={this.handleCloseSecretDialog}
        >
          <div className={classes.center}>
            {this.props.user.secret && (
              <img
                src={"data:image/jpeg;base64," + this.props.user.secret.qrCode}
                alt="secret"
                width={320}
                height={320}
              />
            )}
          </div>
        </Dialog>
      </>
    );
  }
}

FormWrapper.propTypes = {
  classes: PropTypes.object.isRequired,
  disabled: PropTypes.bool.isRequired
};

export default compose(withStyles(styles), withStore, withUser)(FormWrapper);
