import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import TextInput from '../shared/TextInput';
import Form from '../shared/form/Form';
import Select from '../shared/Select';

import withStore from '../../consumers/withStore';
import withUser from '../../consumers/withUser';
import compose from '../../compose';

const styles = theme => ({
  collaborators: {
    display: 'flex',
    flex: 1
  }
});

const INIT_STATE = {
  name: '',
  collaborators: []
};

class FormWrapper extends React.Component {
  state = this.props.data || INIT_STATE;

  handleChange = (name, value) => {
    this.setState({ [name]: value });
  }

  handleSubmit = async () => {
    const { addProject, getProjects, editProject } = this.props.store;
    const { user } = this.props.user;

    try {
      if (this.state._id) {
        await editProject(this.state, user);
      }
      else {
        await addProject(this.state, user);
      }
      getProjects();
      this.setState(INIT_STATE);
      if (this.props.onSubmit) this.props.onSubmit();
    }
    catch (error) {
      this.props.user.showError(error);
    }
  }

  async componentDidMount() {
    const { getUsers } = this.props.user;
    const { user } = this.props.user;
    const filters = [{ field: '_id', op: "ne", value: user._id }];
    try {
      await getUsers(filters);
    }
    catch (error) {
      this.props.user.showError(error);
    }
  }

  render() {
    const { classes } = this.props;
    const { name, collaborators } = this.state;
    const { users } = this.props.user;

    return (
      <Form onSubmit={this.handleSubmit} inline>
        <TextInput
          label="Name"
          value={name}
          field="name"
          onChange={this.handleChange}
          required
        />
        <div className={classes.collaborators}>
          <Select
            label="Collaborators"
            value={collaborators}
            field="collaborators"
            onChange={this.handleChange}
            data={users.data}
            textField="name"
            fullWidth
            multiple
          />
        </div>
      </Form>
    );
  }
}

FormWrapper.propTypes = {
  classes: PropTypes.object.isRequired,
  onSubmit: PropTypes.func
};

export default compose(
  withStyles(styles),
  withStore,
  withUser
)(FormWrapper);