import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import TextInput from "../shared/TextInput";
import NumInput from "../shared/NumInput";
import Form from "../shared/form/Form";

import withStore from "../../consumers/withStore";
import withUser from "../../consumers/withUser";
import compose from "../../compose";
import { getStyles } from '../shared/style';

const styles = theme => ({
  ...getStyles(theme)
});

const INIT_STATE = {
  name: "",
  address: "",
  ico: "",
  dic: "",
  icdph: "",
  rate: 0,
  reportId: "",
  activity: "",
};

class FormWrapper extends React.Component {
  state = this.props.data || INIT_STATE;

  handleChange = (name, value) => {
    this.setState({ [name]: value });
  };

  handleSubmit = async () => {
    const { addClient, getClients, editClient } = this.props.store;
    const { user } = this.props.user;

    try {
      if (this.state._id) {
        await editClient(this.state, user);
      } else {
        await addClient(this.state, user);
      }
      getClients();
      if(this.props.onSubmit) this.props.onSubmit();
      this.setState(INIT_STATE);
    } catch (error) {
      this.props.user.showError(error);
    }
  };

  render() {
    const { classes, disabled } = this.props;
    const { name, address, ico, dic, icdph, rate, reportId, activity } = this.state;

    return (
      <Form onSubmit={this.handleSubmit} disabled={disabled}>
        <div className={classes.row}>
          <div className={classes.col}>
            <TextInput
              label="Name"
              value={name}
              field="name"
              onChange={this.handleChange}
              required
              disabled={disabled}
            />
          </div>
        </div>
        <div className={classes.row}>
          <div className={classes.col}>
            <TextInput
              className={classes.col}
              label="Address"
              value={address}
              field="address"
              onChange={this.handleChange}
              required
              disabled={disabled}
            />
          </div>
        </div>
        <div className={classes.row}>
          <div className={classes.col}>
            <TextInput
              label="IČO"
              value={ico}
              field="ico"
              onChange={this.handleChange}
              required
              disabled={disabled}
            />
          </div>
          <div className={classes.col}>
            <TextInput
              className={classes.col}
              label="DIČ"
              value={dic}
              field="dic"
              onChange={this.handleChange}
              required
              disabled={disabled}
            />
          </div>
          <div className={classes.col}>
            <TextInput
              className={classes.col}
              label="IČ DPH"
              value={icdph}
              field="icdph"
              onChange={this.handleChange}
              required
              disabled={disabled}
            />
          </div>
        </div>
        <div className={classes.row}>
          <div className={classes.col}>
            <TextInput
              label="Activity"
              value={activity}
              field="activity"
              onChange={this.handleChange}
              required
              disabled={disabled}
            />
          </div>
          <div className={classes.col}>
            <NumInput
              label="Rate"
              value={rate}
              field="rate"
              onChange={this.handleChange}
              min={0}
              step={0.5}
              required
            />
          </div>
          <div className={classes.col}>
            <TextInput
              className={classes.col}
              label="ReportId"
              value={reportId}
              field="reportId"
              onChange={this.handleChange}
              required
              disabled={disabled}
            />
          </div>
        </div>
      </Form>
    );
  }
}

FormWrapper.propTypes = {
  classes: PropTypes.object.isRequired,
  onSubmit: PropTypes.func,
  disabled: PropTypes.bool,
  data: PropTypes.object
};

export default compose(withStyles(styles), withStore, withUser)(FormWrapper);
