import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import TextInput from '../shared/TextInput';
import Form from '../shared/form/Form';
import Select from '../shared/Select';

import withStore from '../../consumers/withStore';
import withUser from '../../consumers/withUser';
import compose from '../../compose';

const styles = theme => ({
  name: {
    display: 'flex',
    flex: 1
  }
});

const INIT_STATE = {
  name: '',
  assignedToId: null,
  stateId: 1
};

class FormWrapper extends React.Component {
  state = this.props.data || INIT_STATE;

  handleChange = (name, value) => {
    this.setState({ [name]: value });
  }

  handleSubmit = async () => {
    const { addTask, refreshTasks, editTask } = this.props.store;
    const { user } = this.props.user;

    try {
      if (this.state._id) {
        await editTask(this.state);
      }
      else {
        await addTask(this.state, user, this.props.projectId);
      }
      refreshTasks();
      this.setState(INIT_STATE);
      if (this.props.onSubmit) this.props.onSubmit();
    }
    catch (error) {
      this.props.user.showError(error);
    }
  }

  render() {
    const { classes } = this.props;
    const { states } = this.props.store;
    const { name, assignedToId, stateId } = this.state;

    const { users } = this.props.store;

    return (
      <Form onSubmit={this.handleSubmit} inline>
        <div className={classes.name}>
          <TextInput
            label="Name"
            value={name}
            field="name"
            onChange={this.handleChange}
            required
            fullWidth
          />
        </div>
        <Select
          label="Assigned to"
          value={assignedToId}
          field="assignedToId"
          onChange={this.handleChange}
          data={users.data}
          textField="name"
        />
        <Select
          label="State"
          value={stateId}
          field="stateId"
          onChange={this.handleChange}
          data={states.data}
          required
          textField="name"
        />
      </Form>
    );
  }
}

FormWrapper.propTypes = {
  classes: PropTypes.object.isRequired,
  onSubmit: PropTypes.func,
  projectId: PropTypes.string.isRequired,
};

export default compose(
  withStyles(styles),
  withStore,
  withUser
)(FormWrapper);