import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import Form from "./Form";
import Typography from "@material-ui/core/Typography";
import GithubButton from "../login/GithubButton";
import GoogleButton from '../login/GoogleButton';
import StravaButton from '../login/StravaButton';
import permissions from '../../permissions';

import withUser from "../../consumers/withUser";
import compose from "../../compose";

const styles = theme => ({
  paper: {
    padding: theme.spacing.unit
  },
  heading: {
    marginLeft: theme.spacing.unit
  },
  connected: {
    padding: theme.spacing.unit,
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'flex-start'
  }
});

function CenteredGrid(props) {
  const { user, hasPermission } = props.user;
  const disabled = !hasPermission(permissions.user);

  return (
    <Grid container spacing={16}>
      <Grid item xs={12}>
        <Paper className={props.classes.paper}>
          <Form disabled={disabled} />
        </Paper>
      </Grid>
      <Grid item xs={12}>
        <Paper className={props.classes.connected}>
          <Typography variant="h6" className={props.classes.heading}>
            Connected apps
          </Typography>
          <GithubButton disabled={disabled || user.github} title={props.user.user.github ? props.user.user.github.name : 'Connect Github'} />
          <GoogleButton disabled={disabled || user.google} title={props.user.user.google ? props.user.user.google.name : 'Connect Google'} />
          <div>
            <StravaButton disabled={disabled} title={props.user.user.strava ? `Sync` : 'Connect'} />
            {props.user.user.strava && props.user.user.strava.synced &&
              <span> (last sync {new Date(props.user.user.strava.synced).toLocaleDateString()}) </span>
            }
          </div>
        </Paper>
      </Grid>
    </Grid>
  );
}

CenteredGrid.propTypes = {
  classes: PropTypes.object.isRequired
};

export default compose(withStyles(styles), withUser)(CenteredGrid);