import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import classnames from "classnames";
import Card from "@material-ui/core/Card";
import CardHeader from "@material-ui/core/CardHeader";
import CardMedia from "@material-ui/core/CardMedia";
import CardContent from "@material-ui/core/CardContent";
import CardActions from "@material-ui/core/CardActions";
import Collapse from "@material-ui/core/Collapse";
import Avatar from '../avatar/Avatar';
import IconButton from "@material-ui/core/IconButton";
import red from "@material-ui/core/colors/red";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import VizSensor from 'react-visibility-sensor';

const styles = theme => ({
  card: {
    margin: theme.spacing.unit,
    width: '100%',
    maxWidth: 480
  },
  media: {
    height: 0,
    paddingTop: "56.25%" // 16:9
  },
  actions: {
    display: "flex"
  },
  expand: {
    transform: "rotate(0deg)",
    marginLeft: "auto",
    transition: theme.transitions.create("transform", {
      duration: theme.transitions.duration.shortest
    })
  },
  expandOpen: {
    transform: "rotate(180deg)"
  },
  avatar: {
    backgroundColor: red[500]
  }
});

class RecipeReviewCard extends React.Component {
  state = {
    imgViz: false,
    img: null
  }

  state = { expanded: false };

  handleExpandClick = () => {
    this.setState(state => ({ expanded: !state.expanded }));
  };

  onViz = async (isVisible) => {
    const data = await this.props.onViz();
    this.setState({ imgViz: true });
    
    if(data) {
      var reader = new FileReader();
      reader.onloadend = () => {
        this.setState({ img: reader.result });
      }
      reader.readAsDataURL(data); 
    }
  }

  render() {
    const { classes, actions, children, cardActions, primary, secondary, avatar, defaultAvatar, tooltip, content } = this.props;
    return (
      <VizSensor
        partialVisibility
        onChange={this.onViz}
      >
        <Card className={classes.card}>
          <CardHeader
            avatar={
              <Avatar
                className={classes.bigAvatar}
                defaultValue={defaultAvatar}
                value={avatar}
                tooltip={tooltip}
              />
            }
            action={actions}
            title={primary}
            subheader={secondary}
          />
          <CardMedia
            className={classes.media}
            image={this.state.img || 'https://next.material-ui.com/static/images/cards/paella.jpg'}
            title="media"
            style={{
              opacity: this.state.imgViz ? 1 : 0.25,
              transition: 'opacity 500ms linear'
            }}
          />
          <CardContent>
            {content}
          </CardContent>
          <CardActions className={classes.actions} disableActionSpacing>
            {cardActions}
            {children && (
              <IconButton
                className={classnames(classes.expand, {
                  [classes.expandOpen]: this.state.expanded
                })}
                onClick={this.handleExpandClick}
                aria-expanded={this.state.expanded}
                aria-label="Show more"
              >
                <ExpandMoreIcon />
              </IconButton>
            )}
          </CardActions>
          <Collapse in={this.state.expanded} timeout="auto" unmountOnExit>
            <CardContent>{children}</CardContent>
          </Collapse>
        </Card>
      </VizSensor>
    );
  }
}

RecipeReviewCard.propTypes = {
  classes: PropTypes.object.isRequired,
  primary: PropTypes.string.isRequired,
  secondary: PropTypes.string.isRequired,
  actions: PropTypes.element,
  children: PropTypes.element,
  cardActions: PropTypes.func,
  tooltip: PropTypes.string.isRequired,
  avatar: PropTypes.string.isRequired,
  defaultAvatar: PropTypes.string.isRequired,
  content: PropTypes.element.isRequired,
  onViz: PropTypes.func.isRequired
};

export default withStyles(styles)(RecipeReviewCard);
