import React from "react";
import PropTypes from "prop-types";
import { withStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import Calendar from "../shared/calendar/Calendar";
import FormDialog from '../shared/FormDialog';
import Form from '../events/Form';

import compose from "../../compose";
import withUser from "../../consumers/withUser";
import withStore from "../../consumers/withStore";
import { withTheme } from '@material-ui/core/styles';

const styles = theme => ({
  paper: {
    padding: theme.spacing.unit
  }
});

class CenteredGrid extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      x: null,
      isOpen: false,
      date: null
    };
  }

  getColor = (event) => {
    const { theme } = this.props;

    switch(event.eventTypeId) {
      case 1:
        return theme.palette.primary.main;
      case 2:
      default:
        return theme.palette.secondary.main;
    }
  }

  handleSelect = (x, date) => {
    this.setState({ x, isOpen: true, date })
  }

  handleClose = () => {
    this.setState({ isOpen: false });
  }

  handleChange = async (filters, page, sort) => {
    try {
      return await this.props.store.getEvents(filters, page);
    } catch (error) {
      this.props.user.showError(error);
    }
  };

  render() {
    const { events } = this.props.store;
    const { user } = this.props.user;
    const disabled = this.state.x && this.state.x.userId !== user._id;

    return (
      <Grid container spacing={16}>
        <Grid item xs={12}>
          <Paper className={this.props.classes.paper}>
            <Calendar
              onSelect={this.handleSelect}
              onChange={this.handleChange}
              textField="name"
              data={events}
              getColor={this.getColor}
            />
            <FormDialog
              title="Event"
              onClose={this.handleClose}
              isOpen={this.state.isOpen}
            >
              <Form 
                data={this.state.x} 
                onSubmit={this.handleClose} 
                date={this.state.date} 
                disabled={disabled}
              />
            </FormDialog>
          </Paper>
        </Grid>
      </Grid>
    );
  }
}

CenteredGrid.propTypes = {
  classes: PropTypes.object.isRequired
};

export default compose(withStyles(styles), withUser, withStore, withTheme())(CenteredGrid);
