import React, { Component } from "react";
import PropTypes from "prop-types";
import Paper from "@material-ui/core/Paper";
import { withStyles } from "@material-ui/core/styles";
import withStore from "../../consumers/withStore";
import withUser from "../../consumers/withUser";
import compose from "../../compose";
import { getStyles } from "../shared/style";
import Form from "../shared/form/Form";
import TextInput from "../shared/TextInput";

const styles = theme => ({
  ...getStyles(theme)
});

class LegacyModeExample extends Component {
  constructor(props) {
    super(props);

    this.state = {
      data: ""
    };
  }

  handleSubmit = () => {
    try {
      this.props.user.sign(this.state.data);
    } catch (error) {
      this.props.user.showError(error);
    }
  };

  handleChange = (name, value) => {
    this.setState({ [name]: value });
  };

  render() {
    const { classes } = this.props;

    return (
      <div className={classes.row}>
        <div className={classes.col}>
          <Paper className={classes.paper}>
            <Form onSubmit={this.handleSubmit}>
              <div className={classes.flex}>
                <TextInput
                  label="Data"
                  value={this.state.data}
                  field="data"
                  onChange={this.handleChange}
                  required
                  multiline
                  fullWidth
                  rows={8}
                />
              </div>
            </Form>
          </Paper>
        </div>
        <div className={classes.col}>
          <div className={classes.center}>
            {this.props.user.signature && (
              <img
                src={
                  "data:image/jpeg;base64," + this.props.user.signature.qrCode
                }
                alt="signature"
                width={320}
                height={320}
              />
            )}
          </div>
        </div>
      </div>
    );
  }
}

LegacyModeExample.propTypes = {
  classes: PropTypes.object.isRequired
};

export default compose(
  withStyles(styles),
  withStore,
  withUser
)(LegacyModeExample);
