import React from "react";
import config from "../../config";
import StravaIcon from "../shared/icons/StravaIcon";
import Button from '@material-ui/core/Button';

const GithubButton = props => {
  const handleClick = () => {
    const redirect = process.env.REACT_APP_HOMEPAGE;
    // const redirect = 'http://localhost:3000';
    const url = `https://www.strava.com/oauth/authorize?client_id=${config.strava.id}&response_type=code&scope=read,activity:read&redirect_uri=${redirect}?provider=4`;
    window.location.replace(url);
  };

  if(!config.strava) {
    return null;
  }

  return (
    <Button fullWidth={props.fullWidth} variant="outlined" color="primary" onClick={handleClick} disabled={props.disabled}>
      <StravaIcon fontSize="large" /> <span>{props.title}</span>
    </Button>
  );
};

export default GithubButton;
